@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);

body {
  margin: 0;
  font-family: "Roboto", sans-serif;
}
:root {
  font-size: 15px;
}
@media (min-width: 500px) {
  :root {
    font-size: 18px;
  }
}
@media (min-width: 1000px) {
  :root {
    font-size: 22px;
  }
}
.Typewriter {
  display: -webkit-flex;
  display: flex;
  width: 100%;
  -webkit-justify-content: center;
          justify-content: center;
}

.icon-container{height:60px;margin-left:28px}span{display:block}.icon-container svg{height:100%}.slogans{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center}.slogan{display:-webkit-flex;display:flex;margin-bottom:40px;-webkit-align-items:center;align-items:center;font-size:2.6rem;font-weight:bold;color:#8306b1;pointer-events:none}@media (min-width: 1000px){.slogan{pointer-events:initial}}.slogan div{transition:0.3s color}.slogan div:hover{color:#9d66b1}div{cursor:pointer}.scene__text__underline{border:2px solid #8306b1;border-radius:4px}.scene__text{color:#8306b1;font-size:1.2rem;font-weight:600;position:absolute;bottom:0;-webkit-transform:translate(-50%, 0);transform:translate(-50%, 0);left:50%;width:100%;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.tunnel{width:100%;height:2000px}@media (min-width: 1000px){.tunnel{height:2400px}}.tunnel__text{color:white}.tunnel__sticky{background-color:black;height:100vh;width:100%;position:-webkit-sticky;position:sticky;top:0;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;overflow:hidden}.tunnel__sticky-inner{width:calc(100% - 80px);height:calc(100% - 120px);position:relative;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.tunnel__box__lt{position:absolute;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.tunnel__box__lt svg *{fill:#8306b1}.tunnel__box__lt svg{filter:drop-shadow(1px 0px 12px #8306b1);-webkit-filter:drop-shadow(1px 0px 12px #8306b1);-moz-filter:drop-shadow(1px 0px 12px #8306b1)}.tunnel__box__lt .spotlight__inner__light__bg:after{width:100%;height:100%;content:"";display:block;position:absolute;top:0;left:0;height:100%}.tunnel__box__lt .spotlight__inner__light{-webkit-clip-path:polygon(0 179px, 100% 0, 100% 100%, 0 220px);clip-path:polygon(0 179px, 100% 0, 100% 100%, 0 220px);position:absolute;left:48px}.tunnel__box__lt .spotlight__inner{position:relative;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;-webkit-flex-direction:row;flex-direction:row}.spotlight__inner__light{width:400px;height:400px}.spotlight__inner__light__bg{width:100%;height:100%;position:relative}.tunnel__box__rt{position:absolute;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.tunnel__box__rt svg *{fill:#8306b1}.tunnel__box__rt svg{filter:drop-shadow(1px 0px 12px #8306b1);-webkit-filter:drop-shadow(1px 0px 12px #8306b1);-moz-filter:drop-shadow(1px 0px 12px #8306b1)}.tunnel__box__rt .spotlight__inner__light{-webkit-clip-path:polygon(0 0, 100% 180px, 100% 221px, 0 100%);clip-path:polygon(0 0, 100% 180px, 100% 221px, 0 100%);position:absolute;left:-400px}.tunnel__box__rt svg{-webkit-transform:rotate(180deg);transform:rotate(180deg)}.tunnel__box__rt .spotlight__inner{position:relative;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;-webkit-flex-direction:row-reverse;flex-direction:row-reverse}.tunnel__box__rt .spotlight__inner__light__bg:after{width:100%;height:100%;content:"";display:block;position:absolute;top:0;left:0;height:100%}

.component {
  width: 100%;
  height: 1200px;
  background-color: green;
}

.projects{overflow:hidden;width:100%;background-color:black;height:100vh}.projects__inner{width:100%;height:100%;position:relative;overflow:hidden}.card-container{position:absolute;width:100vw;will-change:transform;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}.card-container img{width:100%}.card-container img{pointer-events:none}.card{background-color:black;max-width:200px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-filter:drop-shadow(1px 0px 12px #8306b1);filter:drop-shadow(1px 0px 12px #8306b1);-webkit-align-items:center;align-items:center;will-change:transform;border-radius:10px}@media (min-width: 1000px){.card{max-width:300px}}.card__link{position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);padding:12px;background-color:#8306b1;color:white;text-decoration:none;border-radius:4px;-webkit-filter:drop-shadow(1px 0px 12px #8306b1);filter:drop-shadow(1px 0px 12px #8306b1);white-space:nowrap}.projects__header{color:#8306b1;font-size:3rem;padding-bottom:30px}

.contact__header{color:#8306b1;font-size:3rem;padding-bottom:30px}

